import React, { useState } from 'react';
import './App.css';
import { NavLink, Route, Switch } from 'react-router-dom';

import Home from './components/Home';
import Phones from './components/Phones';
import NotFound from './components/NotFound';
import PhoneDetailsPage from './components/PhoneDetailsPage';
import Basket from './components/Basket';

const App = () => {
  const [cart, setCart] = useState([]);

  const addToCart = obj => setCart([...cart, obj]);

  return (
    <div className="App">
      <NavLink to="/" exact>Home</NavLink>
      <NavLink to="/phones">Phones</NavLink>
      <NavLink to="/basket">Cart {cart.length}</NavLink>
      <Switch>
        <Route path="/" exact component={Home} />
        <Route path="/phones" exact component={Phones} addToCart={addToCart} />
        <Route path="/phones/:phoneId" component={PhoneDetailsPage} />
        <Route path="/basket" component={Basket} />
        <Route path="/" component={NotFound} />
      </Switch>
    </div>
  );
};

export default App;
