import React from 'react';
import { NavLink } from 'react-router-dom';

import url from './url';

const PhoneCatalog = ({ phones }) => (
  <ul>
    {phones.map(phone => (
      <NavLink to={`/phones/${phone.id}`}>
        <li>
          <img src={url + phone.imageUrl} alt=""/>
          <h2>{phone.name}</h2>
          <p>{phone.snippet}</p>
        </li>
      </NavLink>
    ))}
  </ul>
);

export default PhoneCatalog;
